.bp-table-editing-enabled .bp-table-cell {
  cursor: text; }

.bp-table-selection-enabled .bp-table-cell {
  cursor: cell; }

.bp-table-cell,
.bt-table-row-header-cell {
  display: inline-block;
  height: 20px;
  padding: 0 10px;
  line-height: 20px;
  font-size: 12px; }
  .pt-dark .bp-table-cell:not([class*="pt-intent-"]), .pt-dark
  .bt-table-row-header-cell:not([class*="pt-intent-"]) {
    color: #f5f8fa; }
  .bp-table-cell.pt-intent-primary,
  .bt-table-row-header-cell.pt-intent-primary {
    background-color: rgba(19, 124, 189, 0.1);
    color: #137cbd; }
  .pt-dark .bp-table-cell.pt-intent-primary, .pt-dark
  .bt-table-row-header-cell.pt-intent-primary {
    background: rgba(19, 124, 189, 0.1);
    color: #2b95d6; }
  .bp-table-cell.pt-intent-success,
  .bt-table-row-header-cell.pt-intent-success {
    background-color: rgba(15, 153, 96, 0.1);
    color: #0f9960; }
  .pt-dark .bp-table-cell.pt-intent-success, .pt-dark
  .bt-table-row-header-cell.pt-intent-success {
    background: rgba(15, 153, 96, 0.1);
    color: #15b371; }
  .bp-table-cell.pt-intent-warning,
  .bt-table-row-header-cell.pt-intent-warning {
    background-color: rgba(217, 130, 43, 0.1);
    color: #d9822b; }
  .pt-dark .bp-table-cell.pt-intent-warning, .pt-dark
  .bt-table-row-header-cell.pt-intent-warning {
    background: rgba(217, 130, 43, 0.1);
    color: #f29d49; }
  .bp-table-cell.pt-intent-danger,
  .bt-table-row-header-cell.pt-intent-danger {
    background-color: rgba(219, 55, 55, 0.1);
    color: #db3737; }
  .pt-dark .bp-table-cell.pt-intent-danger, .pt-dark
  .bt-table-row-header-cell.pt-intent-danger {
    background: rgba(219, 55, 55, 0.1);
    color: #f55656; }
  .bp-table-cell.pt-large,
  .pt-large .bp-table-cell,
  .bt-table-row-header-cell.pt-large,
  .pt-large
  .bt-table-row-header-cell {
    height: 30px;
